<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة يوم عمل</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="workPlace"
                  outlined
                  label="مكان العمل"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="documentNumber"
                  outlined
                  label="رقم الاستمارة"
                  :rules="studentRequiredFormRules"
               ></v-text-field>
               <v-row>
                  <v-col cols="3" style="align-items: center; display: flex"
                     ><p style="font-size: 16px">فتره العمل</p></v-col
                  >
                  <v-col cols="4">
                     <v-text-field
                        v-model="workDurationFrom"
                        outlined
                        label="من"
                        :rules="studentRequiredFormRules"
                     ></v-text-field
                  ></v-col>
                  <v-col cols="1" style="align-items: center; display: flex">
                     <p style="font-size: 35px">-</p>
                  </v-col>
                  <v-col cols="4"
                     ><v-text-field
                        v-model="workDurationTo"
                        outlined
                        label="الى"
                        :rules="studentRequiredFormRules"
                     ></v-text-field
                  ></v-col>
               </v-row>

               <v-select
                  v-model="day"
                  :items="days"
                  label="اليوم"
                  outlined
               ></v-select>
               <v-row>
                  <v-col cols="6">
                     <v-select
                        v-model="categoryId"
                        :items="$store.state.categories"
                        label="نوع العمل"
                        item-text="categoryName"
                        item-value="idCategory"
                        @change="selectCategory"
                        outlined
                     ></v-select>
                  </v-col>
                  <v-col cols="6">
                     <v-select
                        v-model="subCategoryId"
                        :items="subCategories"
                        label="عن"
                        :disabled="disabledSubCategory"
                        item-text="subCategoryName"
                        item-value="idSubCategory"
                        outlined
                     ></v-select>
                  </v-col>
               </v-row>

               <v-menu
                  ref="menu1"
                  v-model="menu1"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
               >
                  <template v-slot:activator="{ on, attrs }">
                     <v-text-field
                        v-model="dateFormatted"
                        label="التاريخ"
                        persistent-hint
                        prepend-icon="mdi-calendar"
                        outlined
                        v-bind="attrs"
                        @blur="date = parseDate(dateFormatted)"
                        v-on="on"
                     ></v-text-field>
                  </template>
                  <v-date-picker
                     v-model="date"
                     no-title
                     @input="menu1 = false"
                  ></v-date-picker>
               </v-menu>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة صنف</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   data: (vm) => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
         .toISOString()
         .substr(0, 10),
      dateFormatted: vm.formatDate(
         new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10)
      ),
      categoryName: "",
      categoryId: "",
      workPlace: "",
      disabledSubCategory: true,
      subCategories: [],
      subCategoryId: "",
      documentNumber: "",
      workDurationFrom: "",
      workDurationTo: "",

      days: [
         "السبت",
         "الاحد",
         "الاثنين",
         "الثلاثاء",
         "الاربعاء",
         "الخميس",
         "الجمعة",
      ],
      day: "",
      reportDate: "",
      menu1: false,
   }),
   methods: {
      saveData() {
         let self = this;

         if (
            self.workPlace &&
            self.documentNumber &&
            self.workDurationFrom &&
            self.workDurationTo &&
            self.subCategoryId &&
            self.day
         ) {
            let data = {
               projectId: self.$store.state.projectDetails.idProject,
               workPlace: self.workPlace,
               documentNumber: self.documentNumber,
               workDuration: `${self.workDurationFrom} - ${self.workDurationTo}`,
               day: self.day,
               reportDate: self.date,
               subCategoryId: self.subCategoryId,
               createdBy: self.$store.state.userCredentials.idUser,
            };

            console.log(data);
            this.$http
               .post(`${this.$store.state.apiUrl}/addReport`, data)
               .then((res) => {
                  this.show = false;
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }
      },
      selectCategory() {
         let self = this;
         self.subCategories = this.$store.state.categories.filter(
            (category) => category.idCategory == this.categoryId
         )[0].subCategory;
         self.disabledSubCategory = false;
      },
      formatDate(date) {
         if (!date) return null;

         const [year, month, day] = date.split("-");
         return `${month}/${day}/${year}`;
      },
      parseDate(date) {
         if (!date) return null;

         const [month, day, year] = date.split("/");
         return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
      },
   },
   computed: {
      computedDateFormatted() {
         return this.formatDate(this.date);
      },
   },
   watch: {
      date(val) {
         this.dateFormatted = this.formatDate(this.date);
      },
   },
};
</script>

<style lang="scss" scoped></style>
