<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة صنف جديد</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="categoryName"
                  outlined
                  label="اسم القسم"
                  :rules="studentRequiredFormRules"
               ></v-text-field>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة صنف</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      categoryName: "",
   }),
   methods: {
      saveData() {
         let self = this;
         let data = {
            categoryName: self.categoryName,
         };

         this.$http
            .post(`${this.$store.state.apiUrl}/categories/create`, data)
            .then((res) => {
               this.show = false;
               this.$toasted.success("تم اضافه البيانات");
               this.$emit("reloadTask");
            })
            .catch((err) => {
               this.$toasted.error(" حدث خطأ في البيانات");
            });
      },
   },
};
</script>

<style lang="scss" scoped></style>
