<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="100%">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة مشروع جديد</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="projectName"
                  outlined
                  label="اسم المشروع"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-menu
                  ref="menu1"
                  v-model="menu1"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
               >
                  <template v-slot:activator="{ on, attrs }">
                     <v-text-field
                        v-model="dateFormatted"
                        label="تاريخ بدء المشروع"
                        persistent-hint
                        prepend-icon="mdi-calendar"
                        outlined
                        v-bind="attrs"
                        @blur="date = parseDate(dateFormatted)"
                        v-on="on"
                     ></v-text-field>
                  </template>
                  <v-date-picker
                     v-model="date"
                     no-title
                     @input="menu1 = false"
                  ></v-date-picker>
               </v-menu>
               <v-menu
                  ref="menu2"
                  v-model="menu2"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
               >
                  <template v-slot:activator="{ on, attrs }">
                     <v-text-field
                        v-model="endDateFormatted"
                        label="تاريخ انتهاء المشروع"
                        outlined
                        persistent-hint
                        prepend-icon="mdi-calendar"
                        v-bind="attrs"
                        @blur="endDate = parseDate(endDateFormatted)"
                        v-on="on"
                     ></v-text-field>
                  </template>
                  <v-date-picker
                     v-model="endDate"
                     no-title
                     @input="menu2 = false"
                  ></v-date-picker>
               </v-menu>

               <v-text-field
                  v-model="totalCost"
                  outlined
                  @blur="formatCurrency"
                  label="تكلفة المشروع"
                  prefix="IQD"
               ></v-text-field>

               <v-text-field
                  v-model="workPlace"
                  outlined
                  label="مكان المشروع"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-textarea
                  v-model="note"
                  outlined
                  name="input-7-4"
                  label="الملاحظات"
               ></v-textarea>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة مشروع</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   data: (vm) => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      projectName: "",
      note: "لايوجد",
      totalCost: 0,
      workPlace: "",
      endDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
         .toISOString()
         .substr(0, 10),
      endDateFormatted: vm.formatDate(
         new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10)
      ),
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
         .toISOString()
         .substr(0, 10),
      dateFormatted: vm.formatDate(
         new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10)
      ),
      menu1: false,
      menu2: false,
   }),
   methods: {
      saveData() {
         let self = this;

         if (
            self.projectName &&
            self.date &&
            self.endDate &&
            self.totalCost &&
            self.workPlace
         ) {
            let data = {
               projectName: self.projectName,
               startDate: self.date,
               endDate: self.endDate,
               totalCost: self.totalCost,
               note: self.note,
               workPlace: self.workPlace,
               createdBy: self.$store.state.userCredentials.idUser,
            };
            console.log(data);
            this.$http
               .post(`${this.$store.state.apiUrl}/addProject`, data)
               .then((res) => {
                  this.show = false;
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }
      },
      formatCurrency() {
         let cost = parseFloat(this.totalCost.replace(/,/g, ""))
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

         console.log(cost);
         if (isNaN(this.totalCost)) {
            this.totalCost = 0;
         } else {
            this.totalCost = this.totalCost;
         }
      },
      formatDate(date) {
         if (!date) return null;

         const [year, month, day] = date.split("-");
         return `${month}/${day}/${year}`;
      },
      parseDate(date) {
         if (!date) return null;

         const [month, day, year] = date.split("/");
         return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
      },
   },
   computed: {
      computedDateFormatted() {
         return this.formatDate(this.date);
      },
   },
   watch: {
      date(val) {
         this.dateFormatted = this.formatDate(this.date);
      },
      endDate(val) {
         this.endDateFormatted = this.formatDate(this.endDate);
      },
   },
};
</script>

<style lang="scss" scoped></style>
