<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="vehicleType"
                  outlined
                  label="نوع الالية"
                  :rules="studentRequiredFormRules"
               ></v-text-field>
               <v-row>
                  <v-col cols="6">
                     <v-text-field
                        v-model="fromTime"
                        outlined
                        label="من"
                        type="time"
                        :rules="studentRequiredFormRules"
                     ></v-text-field>
                  </v-col>

                  <v-col cols="6">
                     <v-text-field
                        v-model="toTime"
                        outlined
                        label="الى"
                        type="time"
                        :rules="studentRequiredFormRules"
                     ></v-text-field>
                  </v-col>
               </v-row>

               <v-text-field
                  v-model="number"
                  outlined
                  label="العدد"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="dailyCost"
                  outlined
                  label="الايجار اليومي"
                  @blur="formatCurrency"
                  prefix="IQD"
               ></v-text-field>
               <v-text-field
                  v-model="driverName"
                  outlined
                  label="اسم السائق"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="phone"
                  outlined
                  label="رقم الهاتف"
                  :rules="studentRequiredFormRules"
               ></v-text-field>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      reportInformation: Object,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      vehicleType: "",
      fromTime: "",
      toTime: "",
      number: "",
      dailyCost: "",
      driverName: "",
      phone: "",
   }),
   created() {
      console.log(this.reportInformation);
   },
   methods: {
      saveData() {
         let self = this;
         if (
            self.vehicleType &&
            self.fromTime &&
            self.toTime &&
            self.driverName &&
            self.phone
         ) {
            let data = {
               vehicleType: this.vehicleType,
               fromTime: this.fromTime,
               toTime: this.toTime,
               number: this.number,
               dailyCost: this.dailyCost,
               driverName: this.driverName,
               phone: this.phone,
               reportId: this.reportInformation.report.work[0].idReport,
               createdBy: self.$store.state.userCredentials.idUser,
            };

            this.$http
               .post(`${this.$store.state.apiUrl}/addProjectVehicle`, data)
               .then((res) => {
                  this.show = false;
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }
      },
      formatCurrency() {
         let cost = parseFloat(this.dailyCost.replace(/,/g, ""))
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

         console.log(cost);
         if (isNaN(this.dailyCost)) {
            this.dailyCost = 0;
         } else {
            this.dailyCost = this.dailyCost;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
