<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>الاصناف الثانوية</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <v-card
                  v-for="(sub, index) in item.subCategory"
                  :key="index"
                  style="margin: 10px"
               >
                  <h4 class="pa-2">{{ sub.subCategoryName }}</h4>
               </v-card>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="show = false"> اغلاق </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-btn icon @click="show = true">
         <v-icon color="primary"> mdi-dots-triangle </v-icon>
      </v-btn>
   </div>
</template>

<script>
export default {
   props: {
      item: Object,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
   }),
   methods: {
      saveData() {
         let self = this;
         console.log(this.item);

         let formData = new FormData();
         formData.append("id", this.item.id);
         formData.append("name", this.item.name);
         formData.append("description", this.item.description);
         formData.append("image", this.item.image);

         this.$http
            .put(`${this.$store.state.apiUrl}/categories/update`, formData, {
               headers: {
                  Authorization: `Bearer ${self.$store.state.userCredentials.jwt}`,
               },
            })
            .then((res) => {
               this.show = false;
               this.$toasted.success("تم تعديل البيانات");
               this.$emit("reloadTask");
            })
            .catch((err) => {
               this.$toasted.error(" حدث خطأ في البيانات");
            });
      },
   },
};
</script>

