<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة صنف جديد</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="expenseType"
                  outlined
                  label="نوع النثرية"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="number"
                  outlined
                  label="العدد"
                  type="number"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="cost"
                  outlined
                  label="الاجرة اليومية"
                  @blur="formatCurrency"
                  prefix="IQD"
               ></v-text-field>

               <v-text-field
                  v-model="totalCost"
                  outlined
                  label="المجموع"
                  disabled
                  prefix="IQD"
               ></v-text-field>

               <v-textarea
                  v-model="note"
                  outlined
                  name="input-7-4"
                  label="التفاصيل"
               ></v-textarea>

               <v-text-field
                  v-model="phone"
                  outlined
                  label="الهاتف"
                  :rules="studentRequiredFormRules"
               ></v-text-field>
            </v-card-text>
            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة صنف</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      reportInformation: Object,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      categoryName: "",
      expenseType: "",
      number: "",
      cost: "",
      totalCost: "",
      note: "",
      phone: "",
   }),
   methods: {
      saveData() {
         let self = this;

         if (
            self.expenseType &&
            self.phone &&
            self.number &&
            self.cost &&
            self.note
         ) {
            let data = {
               expenseType: self.expenseType,
               phone: self.phone,
               cost: self.cost,
               totalCost: self.totalCost,
               number: self.number,
               note: self.note,
               reportId: this.reportInformation.report.work[0].idReport,
               createdBy: self.$store.state.userCredentials.idUser,
            };

            self.$http
               .post(`${this.$store.state.apiUrl}/addExpense`, data)
               .then((res) => {
                  this.show = false;
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }
      },
      formatCurrency() {
         let cost = parseFloat(this.cost.replace(/,/g, ""))
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

         console.log(cost);
         if (isNaN(this.cost)) {
            this.cost = 0;
         } else {
            this.cost = this.cost;
            this.totalCost = this.cost * this.number;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
