<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة يد عاملة</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="workerName"
                  outlined
                  label="اسم الفرد"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-row>
                  <v-col cols="6">
                     <v-text-field
                        v-model="fromTime"
                        outlined
                        label="من"
                        type="time"
                        :rules="studentRequiredFormRules"
                     ></v-text-field>
                  </v-col>

                  <v-col cols="6">
                     <v-text-field
                        v-model="toTime"
                        outlined
                        label="الى"
                        type="time"
                        :rules="studentRequiredFormRules"
                     ></v-text-field>
                  </v-col>
               </v-row>

               <v-text-field
                  v-model="workerDuration"
                  outlined
                  label="العدد"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="cost"
                  outlined
                  label="الاجر اليومي"
                  @blur="formatCurrency"
                  prefix="IQD"
               ></v-text-field>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة يد عاملة</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      reportInformation: Object,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      cost: "",
      fromTime: "",
      toTime: "",
      workerDuration: "",
      workerName: "",
   }),
   methods: {
      saveData() {
         let self = this;

         if (self.workerName) {
            let data = {
               workerName: self.workerName,
               cost: self.cost,
               fromTime: self.fromTime,
               toTime: self.toTime,
               workerDuration: self.workerDuration,
               reportId: this.reportInformation.report.work[0].idReport,
               createdBy: self.$store.state.userCredentials.idUser,
            };

            console.log(data);
            this.$http
               .post(`${this.$store.state.apiUrl}/addWorker`, data)
               .then((res) => {
                  this.show = false;
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }
      },
      formatCurrency() {
         let cost = parseFloat(this.cost.replace(/,/g, ""))
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

         console.log(cost);
         if (isNaN(this.cost)) {
            this.cost = 0;
         } else {
            this.cost = this.cost;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
