<template>
   <div>
      <v-container class="container mt-0" fluid>
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="6">
                  <h3 class="warning-color">الايدي العاملة</h3>
               </v-col>

               <v-col cols="6" style="text-align: left">
                  <AddWorker
                     style="display: inline-block"
                     @reloadTask="initialData()"
                     :reportInformation="projectInformation"
                  />
                  <v-tooltip bottom>
                     <template v-slot:activator="{ on, attrs }">
                        <v-btn
                           class="mx-2"
                           fab
                           small
                           color="primary"
                           @click="$router.push('/details')"
                           v-bind="attrs"
                           v-on="on"
                        >
                           <v-icon dark> mdi-arrow-left </v-icon>
                        </v-btn>
                     </template>
                     <span>اضافة صنف</span>
                  </v-tooltip>
               </v-col>
            </v-row>
         </v-card>
      </v-container>
      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-card-title class="warning-color">
               الايدي العاملة
               <v-spacer></v-spacer>
               <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="بحث"
                  single-line
                  hide-details
                  solo
               ></v-text-field>
            </v-card-title>
            <v-data-table
               :headers="headers"
               :items="ItemsFilter"
               :items-per-page="10"
               class="table-content table-warning-color"
            >
               <template v-slot:item.index="{ item, index }">
                  <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
               </template>

               <!-- <template v-slot:header="props">{{ props }}</template> -->

               <template v-slot:item.dailyWork="{ item, index }">
                  <h5 style="display: inline-block">
                     من : {{ item.fromTime }} &nbsp; \ &nbsp;
                  </h5>
                  <h5 style="display: inline-block">الى : {{ item.toTime }}</h5>
                  <h5>العدد : {{ item.number }}</h5>
               </template>

               <template v-slot:item.actions="{ item }">
                  <!-- <v-btn icon @click="dialog = false">
                     <v-icon color="primary" @click="deleteItem(item)">
                        mdi-delete
                     </v-icon>
                  </v-btn> -->
               </template>
            </v-data-table>
         </v-card>
      </v-container>
   </div>
</template>

<script>
import AddWorker from "./../components/AddWorker.vue";
export default {
   components: { AddWorker },
   props: {
      projectInformation: Object,
   },
   data: () => ({
      workers: [],
      search: "",
      headers: [
         { text: "التسلسل", value: "index" },
         { text: "اسم الفرد", value: "workerName" },
         { text: "ساعات العمل", value: "dailyWork" },
         { text: "الاجر اليومي", value: "cost" },
         { text: "الاجراءات", value: "actions" },
      ],
   }),

   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         let self = this;
         console.log(this.projectInformation);
         self.$http
            .get(
               `${self.$store.state.apiUrl}/workerReport/${self.projectInformation.report.work[0].idReport}`
            )
            .then((res) => {
               console.log(res.data);
               self.workers = res.data;
            });
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.workers.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => item.vehicleType.toLowerCase().includes(v));
            });
         } else {
            return this.workers;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
