<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة مادة</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="materialType"
                  outlined
                  label="نوع المادة"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="quantity"
                  outlined
                  label="الكمية"
                  type="number"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="price"
                  outlined
                  label="السعر"
                  @blur="formatCurrency"
                  prefix="IQD"
               ></v-text-field>

               <v-text-field
                  v-model="totalPrice"
                  outlined
                  label="السعر الكلي"
                  disabled
                  prefix="IQD"
               ></v-text-field>

               <v-text-field
                  v-model="driverName"
                  outlined
                  label="اسم السائق"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-textarea
                  v-model="note"
                  outlined
                  name="input-7-4"
                  label="التفاصيل"
               ></v-textarea>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               fab
               small
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-plus </v-icon>
            </v-btn>
         </template>
         <span>اضافة مادة</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      reportInformation: Object,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      materialType: "",
      quantity: 0,
      price: "",
      totalPrice: "",
      driverName: "",
      note: "",
   }),
   methods: {
      saveData() {
         let self = this;

         if (
            self.note &&
            self.quantity &&
            self.price &&
            self.totalPrice &&
            self.driverName &&
            self.materialType
         ) {
            let data = {
               materialType: self.materialType,
               quantity: self.quantity,
               price: self.price,
               totalPrice: self.totalPrice,
               driverName: self.driverName,
               note: self.note,
               reportId: this.reportInformation.report.work[0].idReport,
               createdBy: self.$store.state.userCredentials.idUser,
            };

            this.$http
               .post(`${this.$store.state.apiUrl}/addMaterial`, data)
               .then((res) => {
                  this.show = false;
                  this.$toasted.success("تم اضافه البيانات");
                  this.$emit("reloadTask");
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء ملئ كافة الحقول");
         }
      },
      formatCurrency() {
         let cost = parseFloat(this.price.replace(/,/g, ""))
            .toString()
            .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

         console.log(cost);
         if (isNaN(this.price)) {
            this.price = 0;
         } else {
            this.price = this.price;
            this.totalPrice = this.price * this.quantity;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
