<template>
   <div>
      <v-container class="container" fluid style="background-color: #ffffff">
         <v-row>
            <v-col cols="12" align="center" align-self="center">
               <v-container class="container" fluid>
                  <div>
                     <v-img
                        :src="require('../assets/logo.jpeg')"
                        class="my-3"
                        contain
                        height="200"
                     />
                  </div>
                  <v-text-field
                     solo
                     v-model="userName"
                     label="اسم المستخدم"
                  ></v-text-field>
                  <v-text-field
                     solo
                     v-model="password"
                     type="password"
                     label="كلمة المرور"
                  ></v-text-field>

                  <v-btn block @click="login" color="primary">
                     <v-icon left> mdi-login-variant </v-icon>
                     تسجيل الدخول
                  </v-btn>
               </v-container>
            </v-col>
         </v-row>
      </v-container>
   </div>
</template>

<script>
export default {
   data: () => ({
      password: "",
      userName: "",
   }),
   methods: {
      login() {
         let self = this;
         if (self.userName && self.password) {
            let credentials = {
               password: self.password,
               userName: self.userName,
            };
            self.$http
               .post(`${self.$store.state.apiUrl}/login`, credentials)
               .then((res) => {
                  console.log(res.data);
                  this.$store
                     .dispatch("login", res.data)
                     .then(() => {
                        this.$router.push("/");
                        location.reload();
                     })
                     .catch((e) => {});
               })
               .catch((e) => {
                  console.log(e);
                  this.$toasted.error("حدث خطأ الرجاء التأكد من المعلومات");
               });
         } else {
            this.$toasted.error("الرجاء ادخال  اسم المستخدم وكلمة المرور");
         }
      },
   },
};
</script>

<style>
.register:hover {
   cursor: pointer;
}
</style>
