<template>
   <div v-if="show">
      <v-container class="container mt-0" fluid>
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="6">
                  <h3 class="warning-color">ايام العمل</h3>
               </v-col>

               <v-col cols="6" style="text-align: left">
                  <AddProjectDetails
                     style="display: inline-block"
                     @reloadTask="initialData()"
                  />
                  <v-tooltip bottom>
                     <template v-slot:activator="{ on, attrs }">
                        <v-btn
                           class="mx-2"
                           fab
                           small
                           color="primary"
                           @click="$router.push('/projects')"
                           v-bind="attrs"
                           v-on="on"
                        >
                           <v-icon dark> mdi-arrow-left </v-icon>
                        </v-btn>
                     </template>
                     <span>اضافة صنف</span>
                  </v-tooltip>
               </v-col>
            </v-row>
         </v-card>
      </v-container>
      <v-container class="container mt-0" fluid>
         <v-container class="container" fluid>
            <v-card color="accent" elevation="6">
               <v-card-title class="warning-color">
                  ايام العمل
                  <v-spacer></v-spacer>
                  <v-text-field
                     v-model="search"
                     append-icon="mdi-magnify"
                     label="بحث"
                     single-line
                     hide-details
                     solo
                  ></v-text-field>
               </v-card-title>
               <v-data-table
                  :headers="headers"
                  :items="ItemsFilter"
                  :items-per-page="10"
                  class="table-content table-warning-color"
               >
                  <template v-slot:item.index="{ item, index }">
                     <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
                  </template>

                  <template v-slot:item.actions="{ item }">
                     <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                           <v-btn
                              icon
                              v-bind="attrs"
                              v-on="on"
                              @click="
                                 $router.push({
                                    name: 'WorkDay',
                                    params: { report: { work: workingDays } },
                                 })
                              "
                           >
                              <v-icon color="primary">
                                 mdi-ballot-recount
                              </v-icon>
                           </v-btn>
                        </template>
                        <span>التفاصيل</span>
                     </v-tooltip>

                     <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                           <v-btn
                              icon
                              v-bind="attrs"
                              v-on="on"
                              @click="exportAsExcel(item)"
                           >
                              <v-icon color="primary">
                                 mdi-microsoft-excel
                              </v-icon>
                           </v-btn>
                        </template>
                        <span>تصدير</span>
                     </v-tooltip>
                  </template>
               </v-data-table>
            </v-card>
         </v-container>
      </v-container>
   </div>
</template>

<script>
import AddProjectDetails from "./../components/AddProjectDetails.vue";
const Excel = require("exceljs");
export default {
   components: {
      AddProjectDetails,
   },
   data: () => ({
      project: null,
      workingDays: [],
      show: false,
      search: "",
      headers: [
         { text: "التسلسل", value: "index" },
         { text: "التاريخ", value: "reportDateFormatted" },
         { text: "اليوم", value: "day" },
         { text: "فترة العمل", value: "workDuration" },
         { text: "نوع العمل", value: "categoryName" },
         { text: "عن", value: "subCategoryName" },
         { text: "الاجراءات", value: "actions" },
      ],
   }),
   created() {
      let self = this;
      self.initialData();
      //   vehicle
      //   worker
      //   expense
   },
   methods: {
      initialData() {
         let self = this;

         this.project = this.$store.state.projectDetails;
         self.$http
            .get(
               `${self.$store.state.apiUrl}/reportProject/${self.project.idProject}`
            )
            .then((res) => {
               console.log(res.data);
               this.workingDays = res.data;
               this.show = true;
            });
      },
      async exportAsExcel(item) {
         let self = this;
         console.log(item);
         const workbook = new Excel.Workbook();

         const workersRequest = self.$http.get(
            `${self.$store.state.apiUrl}/workerReport/${item.idReport}`
         );

         const materialRequest = self.$http.get(
            `${self.$store.state.apiUrl}/materialReport/${item.idReport}`
         );

         const expenseRequest = self.$http.get(
            `${self.$store.state.apiUrl}/expenseReport/${item.idReport}`
         );

         const vehicleRequest = self.$http.get(
            `${self.$store.state.apiUrl}/vehicleReport/${item.idReport}`
         );

         Promise.allSettled([
            workersRequest,
            materialRequest,
            expenseRequest,
            vehicleRequest,
         ]).then((responses) => {
            const workerResponse = responses[0];
            const materialResponse = responses[1];
            const expenseResponse = responses[2];
            const vehicleResponse = responses[3];
            let workers =
               workerResponse.status == "fulfilled"
                  ? workerResponse.value.data
                  : [];
            let materials =
               materialResponse.status == "fulfilled"
                  ? materialResponse.value.data
                  : [];
            let expenses =
               expenseResponse.status == "fulfilled"
                  ? expenseResponse.value.data
                  : [];
            let vehicles =
               vehicleResponse.status == "fulfilled"
                  ? vehicleResponse.value.data
                  : [];

            workbook.addWorksheet(`الايدي العاملة`, {
               properties: { defaultColWidth: 30 },
               pageSetup: {
                  paperSize: 9,
                  orientation: "portrait",
                  // fitToPage: true,
                  horizontalCentered: true,
                  verticalCentered: true,
               },
            });

            workbook.addWorksheet(`الاليات`, {
               properties: { defaultColWidth: 30 },
               pageSetup: {
                  paperSize: 9,
                  orientation: "portrait",
                  // fitToPage: true,
                  horizontalCentered: true,
                  verticalCentered: true,
               },
            });

            workbook.addWorksheet(`المصاريف اليومية`, {
               properties: { defaultColWidth: 30 },
               pageSetup: {
                  paperSize: 9,
                  orientation: "portrait",
                  // fitToPage: true,
                  horizontalCentered: true,
                  verticalCentered: true,
               },
            });

            workbook.addWorksheet(`المواد`, {
               properties: { defaultColWidth: 30 },
               pageSetup: {
                  paperSize: 9,
                  orientation: "portrait",
                  // fitToPage: true,
                  horizontalCentered: true,
                  verticalCentered: true,
               },
            });

            let workersSheet = workbook.getWorksheet(`الايدي العاملة`);
            workersSheet.views = [{ rightToLeft: true }];
            let vehiclesSheet = workbook.getWorksheet(`الاليات`);
            vehiclesSheet.views = [{ rightToLeft: true }];
            let materialsSheet = workbook.getWorksheet(`المصاريف اليومية`);
            materialsSheet.views = [{ rightToLeft: true }];
            let expensesSheet = workbook.getWorksheet(`المواد`);
            expensesSheet.views = [{ rightToLeft: true }];

            workersSheet.columns = [
               { header: "ت", key: "id", width: 10 },
               { header: "اسم الفرد", key: "workerName", width: 10 },
               { header: "من", key: "fromTime", width: 32 },
               { header: "الى", key: "toTime", width: 32 },
               { header: "العدد", key: "workerDuration", width: 32 },
               {
                  header: "الاجر اليومي",
                  key: "cost",
                  width: 10,
                  outlineLevel: 1,
               },
            ];
            workersSheet.getRow(1).values = [
               "استمارة تقرير عمل يومي / الايدي العاملة",
               " ",
               `رقم الاستمارة : ${item.documentNumber}`,
            ];
            workers.forEach((worker, index) => {
               workersSheet.addRow({
                  id: index + 1,
                  workerName: worker.workerName,
                  fromTime: worker.fromTime,
                  toTime: worker.toTime,
                  workerDuration: worker.workerDuration,
                  cost: worker.cost,
               });
            });

            vehiclesSheet.columns = [
               { header: "ت", key: "id", width: 10 },
               { header: "اسم الفرد", key: "workerName", width: 10 },
               { header: "من", key: "fromTime", width: 32 },
               { header: "الى", key: "toTime", width: 32 },
               { header: "العدد", key: "workerDuration", width: 32 },
               {
                  header: "الاجر اليومي",
                  key: "cost",
                  width: 10,
                  outlineLevel: 1,
               },
            ];

            workers.forEach((worker, index) => {
               workersSheet.addRow({
                  id: index + 1,
                  workerName: worker.workerName,
                  fromTime: worker.fromTime,
                  toTime: worker.toTime,
                  workerDuration: worker.workerDuration,
                  cost: worker.cost,
               });
            });

            materialsSheet.columns = [
               { header: "ت", key: "id", width: 10 },
               { header: "اسم الفرد", key: "workerName", width: 10 },
               { header: "من", key: "fromTime", width: 32 },
               { header: "الى", key: "toTime", width: 32 },
               { header: "العدد", key: "workerDuration", width: 32 },
               {
                  header: "الاجر اليومي",
                  key: "cost",
                  width: 10,
                  outlineLevel: 1,
               },
            ];

            workers.forEach((worker, index) => {
               workersSheet.addRow({
                  id: index + 1,
                  workerName: worker.workerName,
                  fromTime: worker.fromTime,
                  toTime: worker.toTime,
                  workerDuration: worker.workerDuration,
                  cost: worker.cost,
               });
            });

            expensesSheet.columns = [
               { header: "ت", key: "id", width: 10 },
               { header: "اسم الفرد", key: "workerName", width: 10 },
               { header: "من", key: "fromTime", width: 32 },
               { header: "الى", key: "toTime", width: 32 },
               { header: "العدد", key: "workerDuration", width: 32 },
               {
                  header: "الاجر اليومي",
                  key: "cost",
                  width: 10,
                  outlineLevel: 1,
               },
            ];

            workers.forEach((worker, index) => {
               workersSheet.addRow({
                  id: index + 1,
                  workerName: worker.workerName,
                  fromTime: worker.fromTime,
                  toTime: worker.toTime,
                  workerDuration: worker.workerDuration,
                  cost: worker.cost,
               });
            });

            workbook.xlsx.writeBuffer().then((data) => {
               const blob = new Blob([data], {
                  type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
               });
               let url = window.URL.createObjectURL(blob);
               let a = document.createElement("a");
               document.body.appendChild(a);
               a.setAttribute("style", "display: none");
               a.href = url;
               a.download = `تصدير.xlsx`;
               a.click();
               window.URL.revokeObjectURL(url);
               a.remove();
            });
         });
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.workingDays.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => item.vehicleType.toLowerCase().includes(v));
            });
         } else {
            return this.workingDays;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
